.topics {
  background: #fff;
  border-radius: 5px;
}
.topics ul {
  list-style: none;
  padding-left: 0;
  border-radius: 5px 5px 0 0;
}
.topics ul.tags {
  background: #f6f6f6;
  padding: 10px 20px;
}
.topics ul.tags li {
  display: inline-block;
  margin-right: 20px;
}
.topics ul.tags li a {
  color: #80bd01;
}
.topics ul.tags li a:hover {
  color: #08c;
}
.topics ul.tags li .current-tag {
  background: #80bd01;
  color: #fff;
  padding: 2px 4px;
}
.topics ul.tags li .current-tag:hover {
  color: #fff;
}
.topics button {
  margin: 20px 0;
}
.topics ul.topic-container li {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
.topics ul.topic-container li:after {
  content: '';
  display: block;
  clear: both;
}


