.detail-page:after {
  content: '';
  display: block;
  clear: both;
}
.detail-page .mask {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #eee;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.detail-page .page-content {
  width: 70%;
  padding-right: 30px;
  float: left;
  border-radius: 5px;
  margin-bottom: 10px;
}
.detail-page .page-content .topic-content {
  background: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.detail-page .page-content .topic-content .info {
  font-size: 12px;
  color: #808080;
  padding-bottom: 20px;
  margin-bottom: 0;
  border-bottom: 1px solid #eee;
}
.detail-page .user-aside {
  width: 30%;
  float: left;
}


