.reply {
  background: #fff;
  padding: 10px 15px;
  border-bottom: 1px solid #eee;
}
.reply .btn {
  background: #80bd01;
  color: #fff;
  border-radius: 2px;
  padding: 2px 4px;
  margin: 10px 10px 0 0;
  float: right;
}
.reply .btn:hover {
  background: #6ba44e;
}
.reply .btn.ding {
  display: none;
}
.reply:hover .ding {
  display: block;
}
.reply.show .ding {
  display: block;
}
.reply .author {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.reply .author a {
  display: block;
}
.reply .author a.avatar {
  width: 40px;
}
.reply .author a.name {
  margin-left: 10px;
}
.reply .author .reply-info {
  font-size: 12px;
  color: #666;
  margin-left: 20px;
}
.reply .reply-content {
  padding-left: 50px;
}


