.create-topic {
  padding: 10px 0;
  border-radius: 0 0 3px 3px;
  background: #fff;
  margin-top: 20px;
  width: 30%;
  float: left;
  text-align: center;
}
.create-topic a {
  display: inline-block;
  background: #80bd01;
  padding: 10px 20px;
  border-radius: 3px;
  color: #fff;
}
.create-topic a:hover {
  background: #6ba44e;
}


