.user-page:after {
  content: '';
  display: block;
  clear: both;
}
.user-page .page-content {
  width: 70%;
  float: left;
  padding-right: 30px;
}
.user-page .page-content h3 {
  margin: 0;
  padding: 10px;
  background: #f6f6f6;
  border-radius: 3px 3px 0 0;
}
.user-page .page-content .title {
  background: #f6f6f6;
  padding: 10px;
  border-radius: 3px 3px 0 0;
}
.user-page .page-content .title a {
  color: #80bd01;
  margin-right: 5px;
}
.user-page .page-content .brief {
  background: #fff;
  padding: 10px;
}
.user-page .page-content .brief .author {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.user-page .page-content .brief .author img {
  width: 50px;
  margin-right: 10px;
}
.user-page .page-content .collections h2 {
  font-size: 18px;
}
.user-page .page-content .content-block {
  margin: 20px 0;
}
.user-page .page-content .content-block .detail {
  background: #fff;
  padding: 10px;
}
.user-page .user-aside {
  width: 30%;
  float: left;
}


