* {
  box-sizing: border-box;
}
a {
  color: #333;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
ul,
ol {
  padding-left: 18px;
  margin: 0;
}
body {
  margin: 0;
  color: #333;
  font-size: 14px;
  background: #eee;
}
body .main-content {
  max-width: 960px;
  margin: 0 auto;
}
.footer {
  background: #444;
  padding: 10px 0;
}
.footer p {
  margin: 0;
  color: #fff;
}


