.topic {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 0 20px;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.topic .author a {
  display: block;
}
.topic .author a:hover {
  text-decoration: none;
}
.topic .author a img {
  width: 40px;
  display: block;
}
.topic p {
  margin: 0 10px;
  width: 80px;
}
.topic .tag {
  width: 40px;
  text-align: center;
  background: #80bd01;
  border-radius: 2px;
  color: #fff;
}
.topic .tag.grey {
  background: #aaa;
}
.topic .topic-title {
  margin-left: 10px;
  width: 70%;
}
.topic .topic-title a {
  display: block;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}


