.message-page:after {
  content: '';
  display: block;
  clear: both;
}
.message-page .page-content {
  width: 70%;
  float: left;
  padding-right: 30px;
}
.message-page .new-messages {
  background: #fff;
  margin-bottom: 20px;
}
.message-page .new-messages .title {
  background: #f6f6f6;
  padding: 10px;
}
.message-page .new-messages .title a {
  color: #80bd01;
}
.message-page .new-messages .title span {
  margin-left: 5px;
}
.message-page .old-messages {
  background: #fff;
  margin-bottom: 20px;
}
.message-page .old-messages .title {
  background: #f6f6f6;
  padding: 10px;
}
.message-page .messages-content {
  padding: 10px;
}


