.reply-box {
  background: #fff;
  padding: 20px 0;
}
.reply-box:after {
  content: '';
  display: block;
  clear: both;
}
.reply-box .input-box {
  padding: 0 15px;
}
.reply-box .input-box textarea {
  display: block;
  width: 100%;
  resize: none;
  height: 80px;
  border: 1px solid #ccc;
}
.reply-box .reply-btn {
  display: inline-block;
  background: #80bd01;
  color: #fff;
  border-radius: 2px;
  padding: 2px 4px;
  float: right;
  margin: 20px 15px 0 0;
}
.reply-box .reply-btn:hover {
  background: #6ba44e;
}


