.create-page:after {
  content: '';
  display: block;
  clear: both;
}
.create-page .page-content {
  width: 70%;
  float: left;
  padding-right: 30px;
}
.create-page .title {
  background: #f6f6f6;
  padding: 10px;
}
.create-page .title a {
  color: #80bd01;
}
.create-page .title span {
  margin-left: 5px;
}
.create-page .select-tab {
  margin: 20px 0;
}
.create-page .select-tab span {
  margin-right: 20px;
}
.create-page .topic-title input {
  width: 100%;
  border-radius: 3px;
  border: 1px solid #ccc;
  padding: 0 10px;
  height: 40px;
  margin-bottom: 20px;
}
.create-page .topic-content textarea {
  width: 100%;
  min-height: 400px;
  border: 1px solid #ccc;
  resize: none;
}
.create-page .topic-content .publish-btn {
  background: #80bd01;
  color: #fff;
  padding: 5px 10px;
  text-align: center;
  display: inline-block;
  margin: 10px 0 20px 0;
}
.create-page .topic-content .publish-btn:hover {
  background: #6ba44e;
}
.create-page .preview {
  width: 30%;
  float: left;
  margin-top: 20px;
  background: #fff;
  border: 1px solid #ccc;
  min-height: 380px;
}
.create-page .preview .title {
  padding: 10px;
  background: #f6f6f6;
}
.create-page .preview .content-preview {
  padding: 10px;
}


