.nav {
  background: #444;
  margin-bottom: 30px;
  padding: 10px 0;
}
.nav .main-content:after {
  content: '';
  display: block;
  clear: both;
}
.nav .main-content .logo {
  max-width: 120px;
  float: left;
}
.nav .main-content .logo img {
  width: 100%;
  vertical-align: middle;
}
.nav .main-content a {
  color: #ccc;
}
.nav .main-content .nav-links {
  color: #ccc;
  float: right;
  font-size: 12px;
}
.nav .main-content .nav-links li {
  display: inline-block;
  line-height: 28px;
  height: 28px;
  margin: 0 20px;
}
.nav .main-content .nav-links li:last-child {
  margin-right: 0;
}
.nav .main-content .nav-links li a {
  display: block;
  position: relative;
}
.nav .main-content .nav-links li a .count {
  display: block;
  text-align: center;
  position: absolute;
  right: -15px;
  top: 0;
  width: 16px;
  height: 16px;
  background: #f00;
  color: #fff;
  border-radius: 8px;
}
.nav .main-content .nav-links li a .count .num {
  position: absolute;
  top: -7px;
  left: 5px;
}


